import React, { Fragment, Component } from 'react';
import ProfileAside from './ProfileAside';
import classnames from 'classnames';
import address from './address.json';
import { connect } from 'react-redux';
import { editAddress } from '../../actions/profile';
import PropTypes from 'prop-types';

class EditAddress extends Component {
  state = {
    name: '',
    number: '',
    province: '',
    city: '',
    area: '',
    detail: '',
    currentProvince: '',
    currentCitys: [],
    currentAreas: [],
    errors: {}
  };
  componentWillMount() {
    window.scrollTo(0, 0);
    if (this.props.address.hasAddress) {
      const { name, number, province, city, area, detail } = this.props.address;
      let currentCitys = [];
      for (let key in address[province]) {
        currentCitys.push(key);
      }

      let currentAreas = address[province][city];

      this.setState({
        name,
        number,
        province,
        city,
        area,
        detail,
        currentProvince: province,
        currentCitys,
        currentAreas
      });
    }
  }
  componentDidMount() {}
  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }
  onTextChange(e) {
    this.setState({
      [e.target.name]: e.target.value
    });
  }

  //on province change
  onChange = e => {
    let currentCitys = [];
    for (let key in address[e.target.value]) {
      currentCitys.push(key);
    }
    let currentAreas = address[e.target.value][currentCitys[0]];

    this.setState({
      currentProvince: e.target.value,
      currentCitys,
      currentAreas,
      province: e.target.value,
      city: currentCitys[0],
      area: currentAreas[0]
    });
  };

  onCityChange = e => {
    let currentAreas = address[this.state.currentProvince][e.target.value];
    this.setState({
      currentAreas,
      city: e.target.value,
      area: currentAreas[0]
    });
  };
  onAreaChange = e => {
    this.setState({ area: e.target.value });
  };

  onClick = e => {
    e.preventDefault();
    const { name, number, province, city, area, detail } = this.state;
    console.log(name, number, province, city, area, detail);
    this.props.editAddress(
      { name, number, province, city, area, detail },
      this.props.history
    );
    // console.log(name, number, province, city, area, detail);
  };

  render() {
    let errors = this.state.errors;
    return (
      <Fragment>
        <main className="py-3">
          <p className="border-bottom pb-2 border-secondary"></p>
          <div className="main-container m-auto py-3 text-grey">
            <div className="row">
              <div className="col-lg-3 p-3">
                <ProfileAside props={this.props} isProfile={false} />
              </div>
              <div className="col-lg-9 p-3">
                <div className="border border-secondary p-4 text-left small">
                  <p className=" my-3"></p>

                  <form onSubmit={this.onSubmit}>
                    <p className="border-bottom border-grey"></p>
                    <div
                      className={classnames('form-input-item', {
                        'form-input-error': errors.name
                      })}
                    >
                      <label htmlFor="name" className="d-inline-block pr-4">
                        *
                      </label>
                      <input
                        name="name"
                        value={this.state.name}
                        onChange={e => {
                          this.onTextChange(e);
                        }}
                        className="address-input"
                        type="text"
                      />
                      <p className="invalid-feedback small">{errors.name}</p>
                    </div>
                    <div
                      className={classnames('form-input-item', {
                        'form-input-error': errors.number
                      })}
                    >
                      <label htmlFor="name" className="d-inline-block pr-4">
                        *
                      </label>
                      <input
                        name="number"
                        value={this.state.number}
                        onChange={e => {
                          this.onTextChange(e);
                        }}
                        className="address-input"
                        type="text"
                      />
                      <p className="invalid-feedback small">{errors.number}</p>
                    </div>
                    <p className="border-bottom border-grey pt-5"></p>
                    <div className="row">
                      <div className="col-md-4">
                        <select
                          onChange={e => {
                            this.onChange(e);
                          }}
                          value={this.state.province || ''}
                          name="province"
                          title="/"
                          className={classnames('validate-select', {
                            'select-error': errors.province
                          })}
                        >
                          <option selected disabled>
                            
                          </option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                          <option></option>
                        </select>
                      </div>
                      <div className="col-md-4">
                        <select
                          onChange={e => {
                            this.onCityChange(e);
                          }}
                          value={this.state.city || ''}
                          name="city"
                          className={classnames('validate-select', {
                            'select-error': errors.province
                          })}
                        >
                          <option disabled selected>
                            
                          </option>
                          {this.state.currentCitys.map(city => (
                            <option key={city}>{city}</option>
                          ))}
                        </select>
                      </div>
                      <div className="col-md-4">
                        <select
                          onChange={e => {
                            this.onAreaChange(e);
                          }}
                          value={this.state.area || ''}
                          name="area"
                          className={classnames('validate-select', {
                            'select-error': errors.province
                          })}
                        >
                          <option disabled selected>
                            
                          </option>
                          {this.state.currentAreas.map(area => (
                            <option key={area}>{area}</option>
                          ))}
                        </select>
                      </div>
                    </div>

                    <div
                      className={classnames('form-input-item mt-3', {
                        'form-input-error': errors.detail
                      })}
                    >
                      <label htmlFor="name" className="d-inline-block pr-4">
                        *
                      </label>
                      <input
                        name="detail"
                        value={this.state.detail}
                        onChange={e => {
                          this.onTextChange(e);
                        }}
                        className="address-input"
                        type="text"
                      />
                      <p className="invalid-feedback small">{errors.detail}</p>
                    </div>
                    <div className="d-flex justify-content-end">
                      <button
                        className="submit-button my-3"
                        onClick={this.onClick}
                      >
                        
                      </button>
                    </div>
                  </form>
                </div>
                {/* <div className="border border-secondary mt-3 p-4 small">awd</div> */}
              </div>
            </div>
          </div>
        </main>
      </Fragment>
    );
  }
}

EditAddress.propTypes = {
  editAddress: PropTypes.func.isRequired,
  address: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  address: state.address,
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { editAddress }
)(EditAddress);
